// Libs
import React from "react";
import styled from "styled-components";
import FontAwesomeIcon from "@fortawesome/react-fontawesome";

// Components
import { Video, VideoText, VideoContainer, Bouncy } from "../styles/common";
import ScrollButton from "./ScrollButton";
import {
  faVk,
  // faFacebook,
  faMedium,
  faTwitter
  // faTwitch
} from "@fortawesome/fontawesome-free-brands";
import { faEnvelope } from "@fortawesome/fontawesome-free-solid/";
import logo from "../../public/logo.png";

class VideoOverlay extends React.Component {
  render() {
    return (
      <VideoContainer>
        <Logo src={logo} />
        <SocialIconsRow>
          <SocialIconWrapper
            href={"mailto:bubbletext.ext@gmail.com"}
            target={"_blank"}
          >
            <SocialIcon icon={faEnvelope} size={"2x"} />
          </SocialIconWrapper>

          <SocialIconWrapper
            href={"https://vk.com/bubbletext"}
            target={"_blank"}
          >
            <SocialIcon icon={faVk} size={"2x"} />
          </SocialIconWrapper>

          <SocialIconWrapper
            href={"https://medium.com/bubbletext"}
            target={"_blank"}
          >
            <SocialIcon icon={faMedium} size={"2x"} />
          </SocialIconWrapper>

          <SocialIconWrapper
            href={"https://twitter.com/bubbletext"}
            target={"_blank"}
          >
            <SocialIcon icon={faTwitter} size={"2x"} />
          </SocialIconWrapper>
          {/* <SocialIconWrapper href={"https://medium.com/bubbletext"}>
            <SocialIcon icon={faTwitch} size={"2x"} />
          </SocialIconWrapper> */}
        </SocialIconsRow>
        <VideoText>{this.props.text}</VideoText>
        <Video loop autoPlay muted>
          <source src={this.props.video} type="video/webm" />
          Your browser does not support the video tag.
        </Video>
        <ScrollButton text="Start here" tutorial={this.props.tutorial} />
      </VideoContainer>
    );
  }
}

export const Logo = styled.img`
  position: absolute;
  top: 40px;
  left: 40px;
  height: 80px;
  width: auto;
`;

export const SocialIconsRow = styled.div`
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
`;

export const SocialIconWrapper = styled.a`
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(-45deg, #d91b5b, #262261);
  ${"" /* border: 1px solid #d91b5b; */} border-radius: 10px;
  width: 54px;
  height: 54px;
  ${"" /* padding: 10px; */} color: white;
  margin: 14px;
`;

export const SocialIcon = styled(FontAwesomeIcon)`
  color: white;
`;

export default VideoOverlay;



