import styled, { injectGlobal, keyframes } from "styled-components";
import BurbankBigCondensedBlackWoff from "../../public/fonts/BurbankBigCondensed-Black.woff";
import BG from "../../public/bg.jpg";
import { bounce, merge } from "react-animations";

injectGlobal`
 @font-face {
   font-family: BurbankBigCondensed-Black;
   src: url('${BurbankBigCondensedBlackWoff}') format("woff");
   font-weight: normal;
   font-style: normal;
   font-stretch: normal;
 }`;

const bounceAnimation = keyframes`${bounce}`;

export const Bouncy = styled.div`
  
`;

export const MasterBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  ${"" /* border: 10px solid black; */} font-family: BurbankBigCondensed-Black;
  ${"" /* background-image: url(${BG}); */} background: linear-gradient(#302964, #D4225C);
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
  padding-bottom: 25vh;
`;

export const VideoContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
  font-family: BurbankBigCondensed-Black;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
`;

export const Video = styled.video`
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  z-index: -100;
`;

export const VideoText = styled.div`
  ${"" /* margin-top: 0vh; */} font-size: 80px;
  color: white;
  text-align: center;
  ${"" /* position: absolute; */};
`;

export const Container = styled.div`
  display: flex;
  flex-direction: row;
  width: 1200px;
  height: 500px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  ${"" /* border: 1px solid #493284; */} margin-top: 100px;
  margin-top: 25vh;
`;

export const ImageBlock = styled.div`
  ${"" /* width: 500px; */} height: auto;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1.5;
  height: 500px;
  ${"" /* border: 5px solid blue; */};
`;

export const Image = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 2% 20% 20% 2%;
`;

export const OverlayImage = styled.img`
  width: auto;
  height: 200px;
  position: absolute;
`;

export const OverlayText = styled.div`
  font-size: 60px;
  color: white;
  position: absolute;
`;

export const TextBlock = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  ${"" /* border: 1px solid green; */} text-align: center;
`;

export const Heading = styled.p`
  color: white;
  font-size: 45px;
`;

export const Paragraph = styled.p`
  color: white;
  margin: 10px;
  font-size: 25px;
`;

export const Logo = styled.img`
  margin-top: 100px;
  width: 600px;
  height: auto;
`;

export const Button = styled.button`
  padding: 10px 30px;
  background-color: transparent;
  font-size: 30px;
  border: none;
  outline: none;
  position: absolute;
  bottom: 1vh;
  align-self: center;
  justify-self: center;
  font-family: BurbankBigCondensed-Black;
  color: white;
  animation: 4s ${bounceAnimation};
  animation-iteration-count:infinite;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
`;



