import React from "react";
import ReactDOM from "react-dom";
import {
  Button
} from "../styles/common";

class ScrollButton extends React.Component {
    constructor(props) {
        super(props);
        this.scrollToTutorial = this.scrollToTutorial.bind(this);
    }

    scrollToTutorial() {
        const tutorial = ReactDOM.findDOMNode(this.props.tutorial);
        window.scrollTo({
          top: tutorial.offsetTop,
          behavior: "smooth"
        });
    }

    render() {
      return (
        <Button onClick={this.scrollToTutorial}>{this.props.text}</Button>
      );
  }
}

export default ScrollButton;



