import React from "react";
// import ReactDOM from "react-dom";
import Panel from "./components/Panel";
import VideoOverlay from "./components/VideoOverlay";
import logo from "../public/logo.png";
import bgvideo from "../public/carousel.webm";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import { MasterBox, Logo, VideoContainer } from "./styles/common";
import instr1img from "../public/instr1.png";
import instr2img from "../public/instr2.png";
import instr3img from "../public/instr3.png";
import instr4img from "../public/instr4.png";
import {
  instructionText1,
  instructionText2,
  instructionText3,
  instructionText4
} from "./constants/texts";

class App extends React.Component {
  render() {
    return (
      <div>
        {/* eslint-disable-next-line */}
        <VideoOverlay
          video={bgvideo}
          // eslint-disable-next-line
          text={`Hello, ${streamer}!`}
          tutorial={this.tutorial}
        />
        <MasterBox>
          {/* <Logo src={logo} /> */}
          <Panel
            ref={panel => {
              this.tutorial = panel;
            }}
            heading="Step 1:"
            overlaytext="Config"
            img={instr1img}
            text={instructionText1}
          />
          <Panel
            heading="Step 2:"
            overlaytext="OBS"
            img={instr2img}
            text={instructionText2}
          />
          <Panel
            heading="Step 3:"
            overlaytext="Browser"
            img={instr3img}
            text={instructionText3}
          />
          <Panel
            heading="Step 4:"
            overlaytext="Testing"
            img={instr4img}
            text={instructionText4}
          />
        </MasterBox>
      </div>
    );
  }
}

// ReactDOM.render(<App />, document.getElementById("root"));
export default App;



