import React from "react";
// import dog from "../../public/dog.jpg";
import {
  Paragraph,
  Heading,
  TextBlock,
  ImageBlock,
  OverlayText,
  Image,
  Container
} from "../styles/common";
import ScrollAnimation from "react-animate-on-scroll";

class Panel extends React.Component {
  componentDidMount() {}

  render() {
    return (
      <ScrollAnimation
        offset={250}
        animateIn="fadeIn"
        animateOnce={true}
        delay={300}
      >
        <Container>
          <ImageBlock>
            <Image src={this.props.img} />
            <OverlayText>{this.props.overlaytext}</OverlayText>
          </ImageBlock>
          <TextBlock>
            <Heading>{this.props.heading}</Heading>
            <Paragraph>{this.props.text}</Paragraph>
          </TextBlock>
        </Container>
      </ScrollAnimation>
    );
  }
}

export default Panel;



